@import '../../../sass/consts';

.crowd_funding_detail {
  position: relative;

  .top_tab {
    position: fixed;
    width: 100%;
    line-height: 3rem;
    z-index: 10;

    .am-flexbox-item {
      margin-left: 1.5rem;
      padding: 0 25%;
    }

    img {
      width: 1.5rem;
    }
  }

  .info {
    .title {
    }

    .explain {
      font-size: 0.8rem;
    }
  }

  .goods_num {
    .num {
      background-color: $bg1;

    }
  }

  .crowd_funding {
    margin-top: .5rem;
    padding: .3rem .5rem;
    font-size: .8rem;
    line-height: 1.5rem;

    .price1 {
      padding: 0 .3rem;
    }

    .total {
      //font-size: 1.2rem;
      font-weight: 600;
    }

    .crowd_progress {
      margin: .3rem 0;
      border-radius: .4rem;
      height: .8rem;
      overflow: hidden;

      .am-progress-bar {
        height: .8rem !important;
        background-color: #108ee9;
        border: none;
      }
    }

    .tariff_row {
      text-align: center;

      .tariff_num,
      .tariff_price {
        width: 5rem;
      }
    }
  }

  //.home {
  //  right: calc(100% - 2.5rem);
  //  bottom: calc(100% - 2.5rem);
  //  &.white{
  //    background-color: white;
  //  }
  //}
}
